<template>
  <div class="biorhythm-container">
    <div class="page-header">
      <h1>生物节律查询</h1>
      <p class="subtitle">了解您的身体、情感和智力周期变化</p>
      <div class="divider"></div>
    </div>

    <div class="query-section">
      <div class="query-card">
        <h3>输入您的出生日期</h3>
        <div class="input-group">
          <label for="birthdate">出生日期：</label>
          <input 
            type="date" 
            id="birthdate" 
            v-model="birthdate"
            class="date-input"
          >
        </div>
        <div class="input-group">
          <label for="targetDate">目标日期：</label>
          <input 
            type="date" 
            id="targetDate" 
            v-model="targetDate"
            class="date-input"
          >
        </div>
        <button class="calculate-btn" @click="calculateBiorhythm">计算生物节律</button>
      </div>
    </div>

    <div class="result-section" v-if="showResults">
      <div class="result-card">
        <h3>您的生物节律分析结果</h3>
        <div class="result-date">
          <p>分析日期: {{ formatDate(targetDate) }}</p>
          <p>年龄: {{ age }} 岁</p>
          <p>出生后天数: {{ daysSinceBirth }} 天</p>
        </div>
        
        <div class="biorhythm-chart">
          <canvas ref="biorhythmChart" width="800" height="400"></canvas>
        </div>
        
        <div class="biorhythm-values">
          <div class="value-item physical">
            <div class="value-icon">
              <i class="fas fa-running"></i>
            </div>
            <div class="value-content">
              <h4>身体节律: {{ biorhythm.physical.toFixed(2) }}</h4>
              <div class="progress-bar">
                <div class="progress" :style="{ width: getProgressWidth(biorhythm.physical), backgroundColor: getProgressColor(biorhythm.physical) }"></div>
              </div>
              <p>{{ getPhysicalDescription() }}</p>
            </div>
          </div>
          
          <div class="value-item emotional">
            <div class="value-icon">
              <i class="fas fa-heart"></i>
            </div>
            <div class="value-content">
              <h4>情感节律: {{ biorhythm.emotional.toFixed(2) }}</h4>
              <div class="progress-bar">
                <div class="progress" :style="{ width: getProgressWidth(biorhythm.emotional), backgroundColor: getProgressColor(biorhythm.emotional) }"></div>
              </div>
              <p>{{ getEmotionalDescription() }}</p>
            </div>
          </div>
          
          <div class="value-item intellectual">
            <div class="value-icon">
              <i class="fas fa-brain"></i>
            </div>
            <div class="value-content">
              <h4>智力节律: {{ biorhythm.intellectual.toFixed(2) }}</h4>
              <div class="progress-bar">
                <div class="progress" :style="{ width: getProgressWidth(biorhythm.intellectual), backgroundColor: getProgressColor(biorhythm.intellectual) }"></div>
              </div>
              <p>{{ getIntellectualDescription() }}</p>
            </div>
          </div>
        </div>
        
        <div class="biorhythm-explanation">
          <h3>生物节律说明</h3>
          <p>生物节律理论认为，人体存在三种基本的生理周期：</p>
          <ul>
            <li><strong>身体节律 (23天周期)：</strong> 影响体力、耐力、抵抗力、体能和身体健康状况</li>
            <li><strong>情感节律 (28天周期)：</strong> 影响情绪、感受性、创造力、情感和人际关系</li>
            <li><strong>智力节律 (33天周期)：</strong> 影响思维能力、逻辑分析、学习效率、记忆力和决策能力</li>
          </ul>
          <p>节律值在-1到1之间变化：</p>
          <ul>
            <li><strong>正值 (0到1)：</strong> 表示该方面处于上升或高效期</li>
            <li><strong>零值 (0)：</strong> 表示临界日，该方面可能不稳定或处于转变期</li>
            <li><strong>负值 (-1到0)：</strong> 表示该方面处于下降或低效期</li>
          </ul>
          <p>您可以根据生物节律分析结果，合理安排工作、学习和生活，在高效期完成重要任务，在低效期注意休息和调整。</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js';


export default {
  name: 'BiorhythmQuery',
  data() {
    return {
      birthdate: '',
      targetDate: new Date().toISOString().split('T')[0],
      showResults: false,
      biorhythm: {
        physical: 0,
        emotional: 0,
        intellectual: 0
      },
      daysSinceBirth: 0,
      age: 0,
      chart: null
    };
  },
  methods: {
    calculateBiorhythm() {
      if (!this.birthdate) {
        alert('请输入出生日期');
        return;
      }

      const birthDate = new Date(this.birthdate);
      const targetDate = new Date(this.targetDate);
      
      // 计算出生至今的天数
      const timeDiff = targetDate.getTime() - birthDate.getTime();
      this.daysSinceBirth = Math.floor(timeDiff / (1000 * 3600 * 24));
      
      // 计算年龄
      this.age = this.calculateAge(birthDate, targetDate);
      
      // 计算三种生物节律
      this.biorhythm.physical = Math.sin(2 * Math.PI * this.daysSinceBirth / 23);
      this.biorhythm.emotional = Math.sin(2 * Math.PI * this.daysSinceBirth / 28);
      this.biorhythm.intellectual = Math.sin(2 * Math.PI * this.daysSinceBirth / 33);
      
      this.showResults = true;
      
      // 在下一个DOM更新周期后绘制图表
      this.$nextTick(() => {
        this.drawChart();
      });
    },
    
    calculateAge(birthDate, currentDate) {
      let age = currentDate.getFullYear() - birthDate.getFullYear();
      const m = currentDate.getMonth() - birthDate.getMonth();
      
      if (m < 0 || (m === 0 && currentDate.getDate() < birthDate.getDate())) {
        age--;
      }
      
      return age;
    },
    
    formatDate(dateString) {
      const date = new Date(dateString);
      return `${date.getFullYear()}年${date.getMonth() + 1}月${date.getDate()}日`;
    },
    
    getProgressWidth(value) {
      // 将-1到1的值转换为0到100%的宽度
      return `${((value + 1) / 2 * 100).toFixed(0)}%`;
    },
    
    getProgressColor(value) {
      // 根据值的不同返回不同的颜色
      if (value > 0.5) return '#4CAF50'; // 高值 - 绿色
      if (value > 0) return '#8BC34A'; // 中高值 - 浅绿色
      if (value > -0.5) return '#FFC107'; // 中低值 - 黄色
      return '#F44336'; // 低值 - 红色
    },
    
    getPhysicalDescription() {
      const value = this.biorhythm.physical;
      if (value > 0.7) return '您的身体状态极佳，适合进行体力活动和运动训练。';
      if (value > 0.3) return '您的身体状态良好，精力充沛，适合完成需要体力的工作。';
      if (value > -0.3) return '您的身体状态一般，注意适当休息，不要过度劳累。';
      if (value > -0.7) return '您的身体状态较差，建议减少体力消耗，多休息。';
      return '您的身体状态处于低谷期，应避免剧烈运动，注意身体保养。';
    },
    
    getEmotionalDescription() {
      const value = this.biorhythm.emotional;
      if (value > 0.7) return '您的情绪状态极佳，充满积极性和创造力，适合社交活动。';
      if (value > 0.3) return '您的情绪状态良好，心情愉悦，适合处理人际关系。';
      if (value > -0.3) return '您的情绪状态一般，可能有些波动，注意情绪管理。';
      if (value > -0.7) return '您的情绪状态较差，可能感到焦虑或不安，建议放松心情。';
      return '您的情绪状态处于低谷期，应避免重要的情感决策，多进行自我关怀。';
    },
    
    getIntellectualDescription() {
      const value = this.biorhythm.intellectual;
      if (value > 0.7) return '您的智力状态极佳，思维敏捷，适合学习新知识和解决复杂问题。';
      if (value > 0.3) return '您的智力状态良好，逻辑思维清晰，适合进行分析和决策。';
      if (value > -0.3) return '您的智力状态一般，注意集中注意力，避免复杂的思维活动。';
      if (value > -0.7) return '您的智力状态较差，可能感到思维迟缓，建议简化工作内容。';
      return '您的智力状态处于低谷期，应避免重要的决策和复杂的思维任务。';
    },
    
    drawChart() {
      const ctx = this.$refs.biorhythmChart.getContext('2d');
      
      // 如果已有图表，先销毁
      if (this.chart) {
        this.chart.destroy();
      }
      
      // 生成前后15天的数据
      const days = 31; // 总共显示31天
      const midPoint = Math.floor(days / 2); // 中间点
      
      const labels = [];
      const physicalData = [];
      const emotionalData = [];
      const intellectualData = [];
      
      const targetDateObj = new Date(this.targetDate);
      
      for (let i = 0; i < days; i++) {
        const currentDate = new Date(targetDateObj);
        currentDate.setDate(targetDateObj.getDate() - midPoint + i);
        
        // 添加日期标签
        labels.push(currentDate.getDate() + '日');
        
        // 计算当天的生物节律
        const dayDiff = Math.floor((currentDate - new Date(this.birthdate)) / (1000 * 3600 * 24));
        
        physicalData.push(Math.sin(2 * Math.PI * dayDiff / 23));
        emotionalData.push(Math.sin(2 * Math.PI * dayDiff / 28));
        intellectualData.push(Math.sin(2 * Math.PI * dayDiff / 33));
      }
      
      // 创建图表
      this.chart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: labels,
          datasets: [
            {
              label: '身体节律',
              data: physicalData,
              borderColor: '#F44336',
              backgroundColor: 'rgba(244, 67, 54, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#F44336'
            },
            {
              label: '情感节律',
              data: emotionalData,
              borderColor: '#4CAF50',
              backgroundColor: 'rgba(76, 175, 80, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#4CAF50'
            },
            {
              label: '智力节律',
              data: intellectualData,
              borderColor: '#2196F3',
              backgroundColor: 'rgba(33, 150, 243, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#2196F3'
            }
          ]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            y: {
              min: -1,
              max: 1,
              ticks: {
                callback: function(value) {
                  if (value === 1) return '高峰';
                  if (value === 0) return '临界';
                  if (value === -1) return '低谷';
                  return '';
                }
              },
              grid: {
                color: function(context) {
                  if (context.tick.value === 0) {
                    return 'rgba(0, 0, 0, 0.5)';
                  }
                  return 'rgba(0, 0, 0, 0.1)';
                },
                lineWidth: function(context) {
                  if (context.tick.value === 0) {
                    return 2;
                  }
                  return 1;
                }
              }
            },
            x: {
              grid: {
                display: false
              }
            }
          },
          plugins: {
            tooltip: {
              callbacks: {
                title: function(tooltipItems) {
                  const index = tooltipItems[0].dataIndex;
                  const currentDate = new Date(targetDateObj);
                  currentDate.setDate(targetDateObj.getDate() - midPoint + index);
                  return currentDate.getFullYear() + '年' + 
                         (currentDate.getMonth() + 1) + '月' + 
                         currentDate.getDate() + '日';
                },
                label: function(context) {
                  let label = context.dataset.label || '';
                  if (label) {
                    label += ': ';
                  }
                  if (context.parsed.y !== null) {
                    label += context.parsed.y.toFixed(2);
                  }
                  return label;
                }
              }
            },
            annotation: {
              annotations: {
                line1: {
                  type: 'line',
                  xMin: midPoint,
                  xMax: midPoint,
                  borderColor: 'rgba(0, 0, 0, 0.5)',
                  borderWidth: 2,
                  borderDash: [5, 5],
                  label: {
                    content: '今天',
                    enabled: true,
                    position: 'top'
                  }
                }
              }
            }
          }
        }
      });
    }
  }
};
</script>

<style scoped>
.biorhythm-container {
  padding: 20px;
}

.page-header {
  text-align: center;
  margin-bottom: 30px;
}

.page-header h1 {
  color: #333;
  font-size: 32px;
  margin-bottom: 10px;
}

.subtitle {
  color: #666;
  font-size: 18px;
}

.divider {
  height: 3px;
  width: 100px;
  background: linear-gradient(to right, #8e44ad, #3498db);
  margin: 20px auto;
  border-radius: 3px;
}

.query-section {
  max-width: 600px;
  margin: 0 auto 40px;
}

.query-card {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.query-card h3 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.input-group {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.input-group label {
  width: 100px;
  font-weight: bold;
  color: #555;
}

.date-input {
  flex: 1;
  padding: 10px 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

.calculate-btn {
  display: block;
  width: 100%;
  padding: 12px;
  background: linear-gradient(to right, #8e44ad, #3498db);
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s;
}

.calculate-btn:hover {
  opacity: 0.9;
  transform: scale(1.02);
}

.result-section {
  margin-top: 40px;
}

.result-card {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.result-card h3 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.result-date {
  text-align: center;
  margin-bottom: 30px;
  color: #666;
  font-size: 16px;
}

.biorhythm-chart {
  margin: 30px 0;
  height: 400px;
  position: relative;
}

.biorhythm-values {
  margin: 30px 0;
}

.value-item {
  display: flex;
  margin-bottom: 25px;
  padding: 15px;
  border-radius: 8px;
  background-color: #f9f9f9;
  transition: all 0.3s;
}

.value-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.value-icon {
  flex: 0 0 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  font-size: 24px;
  color: white;
}

.physical .value-icon {
  background: linear-gradient(135deg, #F44336, #FF9800);
}

.emotional .value-icon {
  background: linear-gradient(135deg, #4CAF50, #8BC34A);
}

.intellectual .value-icon {
  background: linear-gradient(135deg, #2196F3, #03A9F4);
}

.value-content {
  flex: 1;
}

.value-content h4 {
  margin-bottom: 10px;
  color: #333;
}

.progress-bar {
  height: 10px;
  background-color: #eee;
  border-radius: 5px;
  margin-bottom: 10px;
  overflow: hidden;
}

.progress {
  height: 100%;
  border-radius: 5px;
  transition: width 0.5s ease;
}

.biorhythm-explanation {
  margin-top: 40px;
  padding-top: 30px;
  border-top: 1px solid #eee;
}

.biorhythm-explanation h3 {
  margin-bottom: 20px;
  color: #333;
}

.biorhythm-explanation p {
  margin-bottom: 15px;
  line-height: 1.6;
  color: #555;
}

.biorhythm-explanation ul {
  margin-bottom: 20px;
  padding-left: 20px;
}

.biorhythm-explanation li {
  margin-bottom: 10px;
  line-height: 1.6;
  color: #555;
}

@media (max-width: 768px) {
  .value-item {
    flex-direction: column;
  }
  
  .value-icon {
    margin: 0 auto 15px;
  }
  
  .value-content {
    text-align: center;
  }
}
</style>


